import React from "react";
import jsonData from "../../json/data.json";
import TransitionLink from "../../hooks/TransitionLink";
const Work = () => {
  return (
    <div className="w-full flex flex-col items-center relative mt-12 ">
      <p className="text-4xl sm:text-5xl xl:text-7xl z-10 mb-10">
        <span className="flex items-center flex-col relative">
          <span className="text-3xl sm:text-4xl text-gradO ">
            Work
          </span>
          <span className="text-7xl text-gradO Alfa text-center">History</span>
        </span>
      </p>
      <div className=" inline-flex max-w-7xl">
        <ul className="flex gap-8 p-4 items-center flex-col md:flex-row">
          {jsonData.work_history.map(({ company, img,link }, index) => (
            <li className={``} key={index}><TransitionLink target='blank' href={link}>
              <img
                className="max-h-[72px] max-w-[144px] box-border transition-all cursor-pointer  hover:contrast-200 hover:scale-150"
                src={img}
                alt={company}
                title={company}
              /></TransitionLink>
            </li>
          ))}
        </ul>
      </div>
    </div>
  );
};

export default Work;
